@import url(https://fonts.googleapis.com/css2?family=Saira:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'Saira', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  font-family: 'Saira', sans-serif;
}

.App {
  background-color: white;
}

#start-section {
  background-color: white;
}

.start-logo {
  width: 100%;
  max-width: 450px;
}

.profile-img {
  width: 100%;
  max-width: 250px;
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.profile-img:hover {
  width: 100%;
  max-width: 250px;
  opacity: 0.75;
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.portfolio-container {
  position: relative;
  max-width: 250px;
}

.portfolio-img {
  opacity: 1;
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: 100%;
  max-width: 250px;
  height: auto;
  transition: .5s ease;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.portfolio-img:hover {
  opacity: 0.3;
}

.portfolio-icon {
  color: white;
  width: 100%;
  max-width: 250px;
  font-size: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.portfolio-btn {
  color: white;
  background-color: transparent;
  border: 0;
  width: 100%;
  max-width: 250px;
  font-size: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.portfolio-btn-close {
  width: 50px;
  height: 50px;
  margin-top: 20px;
  background-color: transparent;
  border: 0;
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.portfolio-transition-html {
  width: 100%;  max-width: 250px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .3s ease;
  background-color: red;
}
.portfolio-transition-html:hover {
  opacity: 0.7;
}

.portfolio-transition-css {
  width: 100%;
  max-width: 250px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .3s ease;
  background-color: blue;
}
.portfolio-transition-css:hover {
  opacity: 0.7;
}

